import React, { useState, useEffect } from 'react';
import { Typography, Box, Container, Paper, Grid, Chip, Divider, List, ListItem, ListItemText, Button } from '@mui/material';
import { useParams, useNavigate } from 'react-router-dom';

interface Recipe {
  id: number;
  title: string;
  image: string;
  ingredients: string[];
  matchedIngredients: string[];
  missingIngredients: string[];
  cuisineType: string;
  prepTime: number;
  cookTime: number;
  instructions: string;
}

const RecipeDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const [recipe, setRecipe] = useState<Recipe | null>(null);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  
  useEffect(() => {
    // Load recipe from localStorage
    const selectedRecipe = localStorage.getItem('selectedRecipe');
    if (selectedRecipe) {
      setRecipe(JSON.parse(selectedRecipe));
    }
    setLoading(false);
  }, [id]);
  
  if (loading) {
    return (
      <Container maxWidth="md">
        <Box sx={{ my: 4, textAlign: 'center' }}>
          <Typography>Loading recipe...</Typography>
        </Box>
      </Container>
    );
  }
  
  if (!recipe) {
    return (
      <Container maxWidth="md">
        <Box sx={{ my: 4, textAlign: 'center' }}>
          <Typography variant="h5" gutterBottom>Recipe Not Found</Typography>
          <Typography paragraph>
            The recipe you're looking for could not be found.
          </Typography>
          <Button 
            variant="contained" 
            color="primary"
            onClick={() => navigate('/recipes')}
          >
            Back to Recipes
          </Button>
        </Box>
      </Container>
    );
  }
  
  // Split instructions into steps
  const instructionSteps = recipe.instructions.split('\n').filter(step => step.trim() !== '');
  
  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Button 
          variant="outlined" 
          onClick={() => navigate('/recipes')}
          sx={{ mb: 2 }}
        >
          Back to Recipes
        </Button>
        
        <Typography variant="h4" component="h1" gutterBottom>
          {recipe.title}
        </Typography>
        
        <Paper sx={{ p: 0, mb: 4, overflow: 'hidden' }}>
          <img 
            src={recipe.image} 
            alt={recipe.title} 
            style={{ width: '100%', height: 'auto', maxHeight: '400px', objectFit: 'cover' }} 
          />
          
          <Box sx={{ p: 3 }}>
            <Grid container spacing={2} sx={{ mb: 3 }}>
              <Grid item xs={4}>
                <Typography variant="body2" color="text.secondary">Cuisine</Typography>
                <Typography variant="body1">{recipe.cuisineType}</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="body2" color="text.secondary">Prep Time</Typography>
                <Typography variant="body1">{recipe.prepTime} minutes</Typography>
              </Grid>
              <Grid item xs={4}>
                <Typography variant="body2" color="text.secondary">Cook Time</Typography>
                <Typography variant="body1">{recipe.cookTime} minutes</Typography>
              </Grid>
            </Grid>
            
            <Divider sx={{ mb: 3 }} />
            
            <Typography variant="h6" gutterBottom>Ingredients</Typography>
            <Grid container spacing={2} sx={{ mb: 3 }}>
              <Grid item xs={12} md={6}>
                <Typography variant="subtitle2" gutterBottom>You have:</Typography>
                <Box>
                  {recipe.matchedIngredients.map((ing) => (
                    <Chip 
                      key={ing} 
                      label={ing} 
                      color="primary" 
                      sx={{ m: 0.5 }} 
                    />
                  ))}
                </Box>
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography variant="subtitle2" gutterBottom>You need:</Typography>
                <Box>
                  {recipe.missingIngredients.map((ing) => (
                    <Chip 
                      key={ing} 
                      label={ing} 
                      variant="outlined" 
                      sx={{ m: 0.5 }} 
                    />
                  ))}
                </Box>
              </Grid>
            </Grid>
            
            <Divider sx={{ mb: 3 }} />
            
            <Typography variant="h6" gutterBottom>Instructions</Typography>
            <List>
              {instructionSteps.map((step, index) => (
                <ListItem key={index} sx={{ py: 1 }}>
                  <ListItemText primary={step} />
                </ListItem>
              ))}
            </List>
            
            {recipe.cuisineType.includes('Fusion') && (
              <Box sx={{ mt: 3, p: 2, bgcolor: 'background.default', borderRadius: 1 }}>
                <Typography variant="subtitle1" gutterBottom>
                  Fusion Recipe Note
                </Typography>
                <Typography variant="body2">
                  This is a fusion recipe combining elements from different cuisines. 
                  Feel free to adjust the seasonings and cooking techniques to match your preferences.
                </Typography>
              </Box>
            )}
          </Box>
        </Paper>
      </Box>
    </Container>
  );
};

export default RecipeDetailPage;
