import React, { useState, useEffect } from 'react';
import { Typography, Box, Container, Paper, Grid, Card, CardMedia, CardContent, CardActions, Button, TextField, InputAdornment, CircularProgress, Chip, FormControl, InputLabel, Select, MenuItem, SelectChangeEvent } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { useNavigate } from 'react-router-dom';

// Mock recipe data
const mockRecipes = [
  {
    id: 1,
    title: "Tomato and Onion Pasta",
    image: "https://images.unsplash.com/photo-1563379926898-05f4575a45d8?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
    ingredients: ["Tomato", "Onion", "Pasta", "Olive Oil", "Garlic", "Basil"],
    matchedIngredients: ["Tomato", "Onion"],
    missingIngredients: ["Pasta", "Olive Oil", "Garlic", "Basil"],
    cuisineType: "Italian",
    prepTime: 15,
    cookTime: 20,
    instructions: "1. Cook pasta according to package instructions.\n2. Sauté onions and garlic in olive oil.\n3. Add diced tomatoes and cook for 10 minutes.\n4. Mix with pasta and garnish with basil."
  },
  {
    id: 2,
    title: "Bell Pepper and Chicken Stir Fry",
    image: "https://images.unsplash.com/photo-1564671165093-20688ff1fffa?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
    ingredients: ["Chicken", "Bell Pepper", "Onion", "Soy Sauce", "Garlic", "Ginger"],
    matchedIngredients: ["Chicken", "Bell Pepper", "Onion"],
    missingIngredients: ["Soy Sauce", "Garlic", "Ginger"],
    cuisineType: "Asian",
    prepTime: 15,
    cookTime: 15,
    instructions: "1. Slice chicken and vegetables.\n2. Heat oil in a wok or large pan.\n3. Stir-fry chicken until cooked through.\n4. Add vegetables and stir-fry for 5 minutes.\n5. Add soy sauce and seasonings."
  },
  {
    id: 3,
    title: "Tomato and Bell Pepper Salad",
    image: "https://images.unsplash.com/photo-1529059997568-3d847b1154f0?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
    ingredients: ["Tomato", "Bell Pepper", "Cucumber", "Olive Oil", "Lemon Juice", "Salt", "Pepper"],
    matchedIngredients: ["Tomato", "Bell Pepper"],
    missingIngredients: ["Cucumber", "Olive Oil", "Lemon Juice", "Salt", "Pepper"],
    cuisineType: "Mediterranean",
    prepTime: 10,
    cookTime: 0,
    instructions: "1. Dice tomatoes, bell peppers, and cucumber.\n2. Mix in a bowl.\n3. Drizzle with olive oil and lemon juice.\n4. Season with salt and pepper."
  },
  {
    id: 4,
    title: "Chicken and Tomato Curry",
    image: "https://images.unsplash.com/photo-1565557623262-b51c2513a641?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
    ingredients: ["Chicken", "Tomato", "Onion", "Curry Powder", "Coconut Milk", "Garlic", "Ginger"],
    matchedIngredients: ["Chicken", "Tomato", "Onion"],
    missingIngredients: ["Curry Powder", "Coconut Milk", "Garlic", "Ginger"],
    cuisineType: "Indian",
    prepTime: 15,
    cookTime: 30,
    instructions: "1. Sauté onions, garlic, and ginger.\n2. Add chicken and cook until browned.\n3. Add curry powder and stir.\n4. Add tomatoes and coconut milk.\n5. Simmer for 25 minutes."
  }
];

// Mock fusion recipe generation
const generateFusionRecipe = (ingredients: string[], cuisine1: string, cuisine2: string) => {
  return {
    id: 999,
    title: `Fusion ${cuisine1}-${cuisine2} with ${ingredients.slice(0, 2).join(" and ")}`,
    image: "https://images.unsplash.com/photo-1547592180-85f173990554?ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
    ingredients: [...ingredients, "Spices", "Herbs", "Oil"],
    matchedIngredients: ingredients,
    missingIngredients: ["Spices", "Herbs", "Oil"],
    cuisineType: `${cuisine1}-${cuisine2} Fusion`,
    prepTime: 20,
    cookTime: 25,
    instructions: `This is a fusion recipe combining elements of ${cuisine1} and ${cuisine2} cuisines using ${ingredients.join(", ")} as the main ingredients.\n\n1. Prepare all ingredients.\n2. Cook according to ${cuisine1} techniques.\n3. Add flavors from ${cuisine2}.\n4. Serve hot and enjoy this unique fusion dish!`
  };
};

interface Recipe {
  id: number;
  title: string;
  image: string;
  ingredients: string[];
  matchedIngredients: string[];
  missingIngredients: string[];
  cuisineType: string;
  prepTime: number;
  cookTime: number;
  instructions: string;
}

const RecipesPage: React.FC = () => {
  const [ingredients, setIngredients] = useState<string[]>([]);
  const [recipes, setRecipes] = useState<Recipe[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [cuisineFilter, setCuisineFilter] = useState('');
  const [showFusionSection, setShowFusionSection] = useState(false);
  const [fusionCuisine1, setFusionCuisine1] = useState('Italian');
  const [fusionCuisine2, setFusionCuisine2] = useState('Asian');
  const [fusionRecipe, setFusionRecipe] = useState<Recipe | null>(null);
  
  const navigate = useNavigate();
  
  // Load ingredients from localStorage on component mount
  useEffect(() => {
    const detectedIngredients = localStorage.getItem('detectedIngredients');
    if (detectedIngredients) {
      const parsedIngredients = JSON.parse(detectedIngredients);
      setIngredients(parsedIngredients.map((ing: any) => ing.name));
    }
    
    // Simulate API call to get recipes
    setTimeout(() => {
      setRecipes(mockRecipes);
      setLoading(false);
    }, 1000);
  }, []);
  
  // Filter recipes based on search term and cuisine filter
  const filteredRecipes = recipes.filter(recipe => {
    const matchesSearch = searchTerm === '' || 
      recipe.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      recipe.ingredients.some(ing => ing.toLowerCase().includes(searchTerm.toLowerCase()));
      
    const matchesCuisine = cuisineFilter === '' || recipe.cuisineType === cuisineFilter;
    
    return matchesSearch && matchesCuisine;
  });
  
  // Sort recipes by number of matched ingredients (descending)
  const sortedRecipes = [...filteredRecipes].sort((a, b) => 
    b.matchedIngredients.length - a.matchedIngredients.length
  );
  
  const handleCuisineFilterChange = (event: SelectChangeEvent) => {
    setCuisineFilter(event.target.value);
  };
  
  const handleFusionCuisine1Change = (event: SelectChangeEvent) => {
    setFusionCuisine1(event.target.value);
  };
  
  const handleFusionCuisine2Change = (event: SelectChangeEvent) => {
    setFusionCuisine2(event.target.value);
  };
  
  const handleGenerateFusion = () => {
    // Generate a fusion recipe using the available ingredients
    const newFusionRecipe = generateFusionRecipe(
      ingredients.length > 0 ? ingredients : ["Tomato", "Onion", "Chicken"],
      fusionCuisine1,
      fusionCuisine2
    );
    setFusionRecipe(newFusionRecipe);
  };
  
  const handleViewRecipe = (id: number) => {
    // Store the selected recipe in localStorage
    const recipe = [...sortedRecipes, fusionRecipe].find(r => r && r.id === id);
    if (recipe) {
      localStorage.setItem('selectedRecipe', JSON.stringify(recipe));
      navigate(`/recipes/${id}`);
    }
  };
  
  return (
    <Container maxWidth="lg">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          Recipe Suggestions
        </Typography>
        <Typography variant="body1" paragraph align="center">
          Discover recipes based on your available ingredients
        </Typography>
        
        <Paper sx={{ p: 3, mb: 4 }}>
          <Box sx={{ mb: 3 }}>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs={12} md={6}>
                <TextField
                  fullWidth
                  label="Search recipes"
                  variant="outlined"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon />
                      </InputAdornment>
                    ),
                  }}
                />
              </Grid>
              <Grid item xs={12} md={4}>
                <FormControl fullWidth>
                  <InputLabel id="cuisine-filter-label">Filter by Cuisine</InputLabel>
                  <Select
                    labelId="cuisine-filter-label"
                    value={cuisineFilter}
                    label="Filter by Cuisine"
                    onChange={handleCuisineFilterChange}
                  >
                    <MenuItem value="">All Cuisines</MenuItem>
                    <MenuItem value="Italian">Italian</MenuItem>
                    <MenuItem value="Asian">Asian</MenuItem>
                    <MenuItem value="Mediterranean">Mediterranean</MenuItem>
                    <MenuItem value="Indian">Indian</MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} md={2}>
                <Button
                  fullWidth
                  variant="outlined"
                  onClick={() => setShowFusionSection(!showFusionSection)}
                >
                  {showFusionSection ? 'Hide Fusion' : 'Create Fusion'}
                </Button>
              </Grid>
            </Grid>
          </Box>
          
          {showFusionSection && (
            <Paper sx={{ p: 2, mb: 3, bgcolor: 'background.default' }}>
              <Typography variant="h6" gutterBottom>
                Create Fusion Recipe
              </Typography>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs={12} sm={4}>
                  <FormControl fullWidth>
                    <InputLabel id="fusion-cuisine-1-label">Cuisine 1</InputLabel>
                    <Select
                      labelId="fusion-cuisine-1-label"
                      value={fusionCuisine1}
                      label="Cuisine 1"
                      onChange={handleFusionCuisine1Change}
                    >
                      <MenuItem value="Italian">Italian</MenuItem>
                      <MenuItem value="Asian">Asian</MenuItem>
                      <MenuItem value="Mediterranean">Mediterranean</MenuItem>
                      <MenuItem value="Indian">Indian</MenuItem>
                      <MenuItem value="Mexican">Mexican</MenuItem>
                      <MenuItem value="French">French</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={4}>
                  <FormControl fullWidth>
                    <InputLabel id="fusion-cuisine-2-label">Cuisine 2</InputLabel>
                    <Select
                      labelId="fusion-cuisine-2-label"
                      value={fusionCuisine2}
                      label="Cuisine 2"
                      onChange={handleFusionCuisine2Change}
                    >
                      <MenuItem value="Italian">Italian</MenuItem>
                      <MenuItem value="Asian">Asian</MenuItem>
                      <MenuItem value="Mediterranean">Mediterranean</MenuItem>
                      <MenuItem value="Indian">Indian</MenuItem>
                      <MenuItem value="Mexican">Mexican</MenuItem>
                      <MenuItem value="French">French</MenuItem>
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} sm={4}>
                  <Button
                    fullWidth
                    variant="contained"
                    color="secondary"
                    onClick={handleGenerateFusion}
                  >
                    Generate Fusion Recipe
                  </Button>
                </Grid>
              </Grid>
              
              {fusionRecipe && (
                <Box sx={{ mt: 3 }}>
                  <Card>
                    <CardMedia
                      component="img"
                      height="200"
                      image={fusionRecipe.image}
                      alt={fusionRecipe.title}
                    />
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        {fusionRecipe.title}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" gutterBottom>
                        {fusionRecipe.cuisineType} • Prep: {fusionRecipe.prepTime} min • Cook: {fusionRecipe.cookTime} min
                      </Typography>
                      <Box sx={{ mb: 1 }}>
                        <Typography variant="body2" component="span">
                          Matched ingredients: 
                        </Typography>
                        {fusionRecipe.matchedIngredients.map((ing) => (
                          <Chip 
                            key={ing} 
                            label={ing} 
                            size="small" 
                            color="primary" 
                            sx={{ m: 0.5 }} 
                          />
                        ))}
                      </Box>
                    </CardContent>
                    <CardActions>
                      <Button 
                        size="small" 
                        color="primary"
                        onClick={() => handleViewRecipe(fusionRecipe.id)}
                      >
                        View Recipe
                      </Button>
                    </CardActions>
                  </Card>
                </Box>
              )}
            </Paper>
          )}
          
          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : sortedRecipes.length > 0 ? (
            <Grid container spacing={3}>
              {sortedRecipes.map((recipe) => (
                <Grid item key={recipe.id} xs={12} sm={6} md={4}>
                  <Card>
                    <CardMedia
                      component="img"
                      height="200"
                      image={recipe.image}
                      alt={recipe.title}
                    />
                    <CardContent>
                      <Typography gutterBottom variant="h5" component="div">
                        {recipe.title}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" gutterBottom>
                        {recipe.cuisineType} • Prep: {recipe.prepTime} min • Cook: {recipe.cookTime} min
                      </Typography>
                      <Box sx={{ mb: 1 }}>
                        <Typography variant="body2" component="span">
                          Matched ingredients: 
                        </Typography>
                        {recipe.matchedIngredients.map((ing) => (
                          <Chip 
                            key={ing} 
                            label={ing} 
                            size="small" 
                            color="primary" 
                            sx={{ m: 0.5 }} 
                          />
                        ))}
                      </Box>
                      <Box>
                        <Typography variant="body2" component="span">
                          Missing ingredients: 
                        </Typography>
                        {recipe.missingIngredients.map((ing) => (
                          <Chip 
                            key={ing} 
                            label={ing} 
                            size="small" 
                            variant="outlined" 
                            sx={{ m: 0.5 }} 
                          />
                        ))}
                      </Box>
                    </CardContent>
                    <CardActions>
                      <Button 
                        size="small" 
                        color="primary"
                        onClick={() => handleViewRecipe(recipe.id)}
                      >
                        View Recipe
                      </Button>
                    </CardActions>
                  </Card>
                </Grid>
              ))}
            </Grid>
          ) : (
            <Box sx={{ textAlign: 'center', py: 4 }}>
              <Typography variant="body1" color="text.secondary">
                No recipes found matching your criteria. Try adjusting your search or adding more ingredients.
              </Typography>
              <Button 
                variant="outlined" 
                sx={{ mt: 2 }}
                onClick={() => navigate('/ingredients')}
              >
                Manage Ingredients
              </Button>
            </Box>
          )}
        </Paper>
      </Box>
    </Container>
  );
};

export default RecipesPage;
