import React, { useState, useEffect } from 'react';
import { Typography, Box, Container, Paper, List, ListItem, ListItemText, ListItemSecondaryAction, IconButton, Button, TextField, Grid, Chip, Divider } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router-dom';

interface Ingredient {
  id: number;
  name: string;
  confidence?: number;
}

const IngredientsPage: React.FC = () => {
  const [ingredients, setIngredients] = useState<Ingredient[]>([]);
  const [newIngredient, setNewIngredient] = useState('');
  const navigate = useNavigate();
  
  // Load detected ingredients from localStorage on component mount
  useEffect(() => {
    const detectedIngredients = localStorage.getItem('detectedIngredients');
    if (detectedIngredients) {
      setIngredients(JSON.parse(detectedIngredients));
    }
  }, []);
  
  // Save ingredients to localStorage whenever they change
  useEffect(() => {
    localStorage.setItem('detectedIngredients', JSON.stringify(ingredients));
  }, [ingredients]);
  
  const handleAddIngredient = () => {
    if (newIngredient.trim()) {
      const newId = ingredients.length > 0 
        ? Math.max(...ingredients.map(i => i.id)) + 1 
        : 1;
        
      setIngredients([...ingredients, { id: newId, name: newIngredient.trim() }]);
      setNewIngredient('');
    }
  };
  
  const handleDeleteIngredient = (id: number) => {
    setIngredients(ingredients.filter(ingredient => ingredient.id !== id));
  };
  
  const handleFindRecipes = () => {
    navigate('/recipes');
  };
  
  const handleClearAll = () => {
    setIngredients([]);
  };
  
  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          Your Ingredients
        </Typography>
        <Typography variant="body1" paragraph align="center">
          Manage your available ingredients to find matching recipes
        </Typography>
        
        <Paper sx={{ p: 3, mb: 4 }}>
          <Box sx={{ mb: 3 }}>
            <Grid container spacing={2} alignItems="center">
              <Grid item xs>
                <TextField
                  fullWidth
                  label="Add ingredient"
                  variant="outlined"
                  value={newIngredient}
                  onChange={(e) => setNewIngredient(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      handleAddIngredient();
                    }
                  }}
                />
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="primary"
                  startIcon={<AddIcon />}
                  onClick={handleAddIngredient}
                >
                  Add
                </Button>
              </Grid>
            </Grid>
          </Box>
          
          <Divider sx={{ mb: 3 }} />
          
          {ingredients.length > 0 ? (
            <List>
              {ingredients.map((ingredient) => (
                <ListItem key={ingredient.id} divider>
                  <ListItemText 
                    primary={ingredient.name} 
                    secondary={ingredient.confidence ? `Confidence: ${Math.round(ingredient.confidence * 100)}%` : null} 
                  />
                  <ListItemSecondaryAction>
                    <IconButton 
                      edge="end" 
                      aria-label="delete"
                      onClick={() => handleDeleteIngredient(ingredient.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          ) : (
            <Box sx={{ textAlign: 'center', py: 4 }}>
              <Typography variant="body1" color="text.secondary">
                No ingredients added yet. Add ingredients manually or use the camera to detect them.
              </Typography>
              <Button 
                variant="outlined" 
                sx={{ mt: 2 }}
                onClick={() => navigate('/camera')}
              >
                Use Camera
              </Button>
            </Box>
          )}
          
          {ingredients.length > 0 && (
            <Box sx={{ mt: 3, display: 'flex', justifyContent: 'space-between' }}>
              <Button 
                variant="outlined" 
                color="error"
                onClick={handleClearAll}
              >
                Clear All
              </Button>
              <Button 
                variant="contained" 
                color="primary"
                onClick={handleFindRecipes}
              >
                Find Recipes
              </Button>
            </Box>
          )}
        </Paper>
        
        <Box sx={{ mt: 4 }}>
          <Typography variant="h6" gutterBottom>
            Common Ingredients
          </Typography>
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
            {['Chicken', 'Beef', 'Tomato', 'Onion', 'Garlic', 'Rice', 'Pasta', 'Potato', 'Carrot', 'Bell Pepper', 'Cheese', 'Egg', 'Milk', 'Butter', 'Olive Oil'].map((item) => (
              <Chip 
                key={item} 
                label={item} 
                onClick={() => setNewIngredient(item)}
                sx={{ m: 0.5 }}
              />
            ))}
          </Box>
        </Box>
      </Box>
    </Container>
  );
};

export default IngredientsPage;
