import React, { useState, useRef, useCallback } from 'react';
import { Typography, Box, Container, Paper, Button, Grid } from '@mui/material';
import Webcam from 'react-webcam';
import { useNavigate } from 'react-router-dom';

// Mock image recognition service
const mockRecognizeIngredients = async (imageData: string) => {
  // In a real app, this would call an API
  console.log("Processing image...");
  
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 1500));
  
  // Return mock detected ingredients
  return [
    { id: 1, name: "Tomato", confidence: 0.95 },
    { id: 2, name: "Onion", confidence: 0.92 },
    { id: 3, name: "Bell Pepper", confidence: 0.88 },
    { id: 4, name: "Chicken", confidence: 0.85 }
  ];
};

const CameraPage: React.FC = () => {
  const webcamRef = useRef<Webcam>(null);
  const [capturedImage, setCapturedImage] = useState<string | null>(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const navigate = useNavigate();
  
  const capture = useCallback(() => {
    if (webcamRef.current) {
      const imageSrc = webcamRef.current.getScreenshot();
      setCapturedImage(imageSrc);
    }
  }, [webcamRef]);
  
  const retake = () => {
    setCapturedImage(null);
  };
  
  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files[0]) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target && typeof e.target.result === 'string') {
          setCapturedImage(e.target.result);
        }
      };
      reader.readAsDataURL(event.target.files[0]);
    }
  };
  
  const processImage = async () => {
    if (capturedImage) {
      setIsProcessing(true);
      try {
        // Process the image to detect ingredients
        const ingredients = await mockRecognizeIngredients(capturedImage);
        
        // Store ingredients in localStorage (in a real app, this would use context or state management)
        localStorage.setItem('detectedIngredients', JSON.stringify(ingredients));
        
        // Navigate to ingredients page
        navigate('/ingredients');
      } catch (error) {
        console.error("Error processing image:", error);
        alert("There was an error processing the image. Please try again.");
      } finally {
        setIsProcessing(false);
      }
    }
  };
  
  return (
    <Container maxWidth="md">
      <Box sx={{ my: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          Capture Ingredients
        </Typography>
        <Typography variant="body1" paragraph align="center">
          Take a photo of your ingredients or upload an image to get started
        </Typography>
        
        <Paper sx={{ p: 3, mb: 3 }}>
          {!capturedImage ? (
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <Webcam
                audio={false}
                ref={webcamRef}
                screenshotFormat="image/jpeg"
                videoConstraints={{ facingMode: "environment" }}
                style={{ width: '100%', maxWidth: '500px', marginBottom: '16px' }}
              />
              <Button 
                variant="contained" 
                color="primary" 
                onClick={capture}
                sx={{ mb: 2 }}
              >
                Capture Photo
              </Button>
              
              <Typography variant="body2" sx={{ mb: 2, textAlign: 'center' }}>
                Or upload an image from your device
              </Typography>
              
              <Button
                variant="outlined"
                component="label"
              >
                Upload Image
                <input
                  type="file"
                  hidden
                  accept="image/*"
                  onChange={handleFileUpload}
                />
              </Button>
            </Box>
          ) : (
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
              <img 
                src={capturedImage} 
                alt="Captured ingredients" 
                style={{ width: '100%', maxWidth: '500px', marginBottom: '16px' }} 
              />
              
              <Grid container spacing={2} justifyContent="center">
                <Grid item>
                  <Button 
                    variant="outlined" 
                    onClick={retake}
                    disabled={isProcessing}
                  >
                    Retake Photo
                  </Button>
                </Grid>
                <Grid item>
                  <Button 
                    variant="contained" 
                    color="primary" 
                    onClick={processImage}
                    disabled={isProcessing}
                  >
                    {isProcessing ? 'Processing...' : 'Recognize Ingredients'}
                  </Button>
                </Grid>
              </Grid>
            </Box>
          )}
        </Paper>
        
        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <Typography variant="body2" color="text.secondary">
            For best results, ensure good lighting and place ingredients on a clear surface.
          </Typography>
        </Box>
      </Box>
    </Container>
  );
};

export default CameraPage;
